            Sabah 5:00dan 6:00a kadar sren ksa bir uykuya daldm. Kalktmda evrem sessizdi, mutfaktan bal ve tereya aldm ve camn kenarna getim. Neredeyse her sabah bal yerim, burada kaldm srete imdi yeme frsatm oluyor. Hava, dn incelediim tahminlerle uyuuyordu. Erkenden teras planlar kurarken, bulutlu havann gn boyunca devam etmesi, gnein mercek zerinde parlaklk yapmayacana iaretti. Dinliimin sebebi uyuduum saat aralyla m ilgili, bilmiyorum. Srekli ve dzenli alarak bal reten arlarn motivasyonunun ksa sreli yanltc etkisi de olabilir. 9:05te terastaydm, drbnn ayaklarn kurmaya balarken 3 ara ieri girdi. Askeri ara, servis arac ve ambulans bir aradayd. Girie en yakn mavi askeri aracn ierisinden siyah niformal bir gardiyan kt, onu karlamak iin binadan kan gardiyan ayn niformay giyiyordu. Nakil srecine elik etmek iin mavi niforma giyen gardiyanlarn says oalmaya balad. niformalarn renklerini belirli grevler zerinden ayrtrmaya balamtm. Siyah niformallar, i mekndaki kontrol salayan ve mavi niformallardan sorumlu olan gardiyan grubuydu, saylar azd. Genellikle nakliye sresince emirleri onlar veriyor, mavi niformallarsa onlar takip ediyorlard. Arabalarn evresinde toplanan gardiyanlar 9:15e kadar aralarnda konutular. ki farkl renkteki niformalar i ie gemi ve gardiyanlar ayrt etmemi zorlatrmt. Saat 9:20de 4 gardiyan, ieriden siyah hrkal ve yeil pantolonlu biriyle dar ktlar, mahkmun ellerindeki kelepe evresindeki kalabalk iinden, merceimde parlamt, ya 25 civarnda olabilir. Genellikle mahkmlar askeri araca bindirilirken bu sefer mahkm, <inline-image-link
                :image-i-d="'IMG_1237'">beyaz</inline-image-link>
            renkteki servis aracna bindirildi. Servisin camlar filmlenmi ve st ince metal tellerle rlmt, ieriyi ancak alan arka kapsndan grebildim. Ambulans ve askeri araca baka kimse tanmad, mavi niformal gardiyanlar boalan iki araca bindikten sonra aralar k yapt. Bu aralarn bazlar, cezaevi ierisinde gardiyanlarn ulamn salamak iin kullanlyor. Saat 9:45 olduunda bir kamyonet girie doru yaklat, normalde bu saatlerde p arac bekliyordum, ieri giren beyaz kamyonete nceki gnlerde rastlamamtm. Ara, nakliye alanna ok yaknlasa da dar bir aralktan nakil ilemlerinin yapld kapy grebiliyordum. Arala odam sabitledim ve bekledim, glgede kalan bu aralktan turkuaz rengi, evresini saran dokulara kontrast bir biimde ortaya kmt. Bir insann boyuna edeer turkuaz renginde kplerdi, arala doru yaknlaarak mercei netletirdiimde bu hacmi, plastik su ielerinin oluturduunu fark ettim. st ste ve yan yana sk skya hizalanm bu pet ieler birbirlerine sarlm tanyordu. O kadar fazla sayda paket tanyordu ki cezaevinin leini ve ierideki kiilerin saysn bir anlna hayal ettim. Bu paketler, aklmdan km bir lei bana basite <inline-image-link
