 teras grdnde olmaz byle ey dedi. Bu srecin nasl devam edeceini tahmin edebiliyordum. Onlar telefonun bandayken eyalarm toplayp gitmeye hazrlanmtm, en azndan bu diyalou daireme tamak istemediim iin bilgi vermek istemiyordum. Ben herkese veda ederken, telefonla konuan yetkili daire numaram sordu, ben de ilikisiz bir daire numaras syleyerek oradan uzaklatm ve daireme doru yola ktm, arkamdan tekrar soru sormadlar. Bu saati dier temizlik saatleri dnda not aldm, bu saat aralnda terasta olduum srece dikkatli olabilirim. Ertesi gn buradaki konaklamamn son gnyd. k yapana kadar sremin ounu dairede geirmeyi dndm, en azndan bu kadar yakn bir zamanda yaadm diyalog sonrasnda tekrar grnr olmak istemedim. Saat 16:10, terastan daireye gelmemin zerinden 10-15 dakika geti, terastaki karlamamdan sonra drbne tekrar bakmak beni sakinletirmiti. Bir sre cezaevinde kimseyi grmezken, genellikle her gece bilgisayarma yedeklediim grselleri yedeklemeye devam etmek iin bilgisayarm atm. Bilgisayarmn arj azd ve arj etmek iin taktm adaptrn almadn fark ettim, kablomun zamanla andn srekli grebiliyordum fakat kullanm anlamnda ilk kez ilememeye balamt. Bir anlk, dosyalarm nasl aktarrm diye dnrken, adaptrn kablolaryla oynamaya baladm. Drbnn netlik ayarna benzer bir iilikle kablonun iindeki bakr telleri belli bir ada tutup bantladm, sonrasnda duvara doru bastrp arj n grdm bir anda sabitledim. arj aleti altktan sonra kabloya yarna kadar hi dokunmay dnmyorum. Saat 16:30, 2 ziyareti k yapt, terasta yaanan durumdan sonra girilerini karm olabilirim. Yamur iddetini azaltsa da youn bir biimde devam ediyordu. Otoyollarda olumu su birikintileri gittike derinlemi, nmdeki inaatta amura dnm toprak alanlar yavaa inaatn temelindeki boluklara szmt. 2. avluda hava kararmaya yakn, gri boazl kaza olan gencin sigara imek iin dar ktn fark ettim; 2 gn nce bu saatlerde duvara ok yaklaarak turlayan mahkmdu ve yannda birileri yoktu. 2. avluya kan mahkmlar genellikle grup halinde grrdm, aralarnda belirli saatlerde avluya yalnz kan bu gen mahkumdu. Yakn zaman aralnda 5. avluda duran sandalyeler lacivert spor eofmanl biri tarafndan ieri alnd. Avlularda, zerinde minder olan btn sandalyelerin minderleri yamurdan renk deitirmiti bile. Saat 17:00a yaklarken, uzaktaki otoyolun yol ayrmndaki iki fosforlu
            <inline-image-link :image-i-d="'IMG_2640'">niformal</inline-image-link>
